a {
	text-decoration: none;
	color: black;
}

ul {
	padding-inline-start: 0;
}

li {
	list-style: none;
	margin: 0px 15px 0px 34px;
}
h2 {
	margin-block-start: 0em;
    margin-block-end: 0em;
}

body {
	margin: 0;
	height: 2000px;
}

.header {
	width: 100%;
	height: fit-content;
	position: absolute;
	z-index: 5;
}
.header-links {
	background: #000;
	height: 60px;
	display: flex;
	justify-content: center;
}
.header-links-block {
	display: flex;
	width: 1200px;
	justify-content: space-between;
	&__content{
		justify-content: space-around;
		display: flex;
		color: #fff;
		font-family: Arial, Helvetica, sans-serif;
		&__phone{
			display: flex;
			margin: 0 15px;
			
			img{
				width: 15px;
			}
		}
		&__mail {
			margin: 0 15px;
			p {
				color: #fff;
			}
		}
		&__location {
			margin: 0 15px;
		}
	}

	&__links {
		display: flex;
		.facebook {
			width: 30px;
			justify-content: center;		}
		.twitter {
			width: 30px;
			img{
			width: 30px;
			margin: 8px 0;
		}
	}
		.instagram img {
			width: 30px;
			margin: 8px 0;
		}
		img{
			width: 15px;
			margin: 15px 0;
		}
	}
}
.header-menu {
	width: 1200px;
	margin: 0 auto;
	padding: 25px 15px;
	display: flex;
	justify-content: space-between;
	&-box {
		background-color: #0000004d;
		height: 103px;
	}
	&__list {
		float: right;
		padding-inline-start: 0;

		li {
			float: left;
			justify-content: space-between;
			margin-left: 10px;
			color: #fff;
			cursor: pointer;
			font-family: 'Oswald';
			font-size: 17px;
			letter-spacing: 1.2px;
			margin: 0px 15px 0px 34px;
		}

		:hover {
			color: #f7992b;
		}
	}
}

.slider-container {
	background: #000;
	height: 980px;
	overflow: hidden;
	img{
		width: 100%;
		margin-top: -117px;
		opacity: 0.5;
	}
}

.slide{

	&-description {
		position: absolute;
    z-index: 50;
    width: 100%;
    // padding: 0px 29%;
		top: 420px;
	}

	&-btn {
		display: block;
		margin: 50px auto;
		width: 227px;
		height: 61px;
		background-color: #f7992b;
		border: solid 1px #f7992b;
		border-radius: 3px;
		color: white;
		font-family: 'Oswald', sans-serif;
		font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.71em;
		letter-spacing: 8.4px;
	}

	h2 {
		font-family: 'Oswald', sans-serif;
		color: white;
		font-size: 80px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 90px;
		letter-spacing: 4px;
		align-self: center;
		text-align: center;
		margin-bottom: 10px;
	}
	p {
		align-self: center;
    text-align: center;
    color: #ffffff;
    font-family: "Oswald", Sans-serif;
    font-size: 38px;
    font-weight: 300;
    line-height: 42px;
    letter-spacing: 1.4px;
    padding: 0px 0px 0px 0px;
		margin: 0px 0px 0px 0px;
	}
}

.about-company {
	margin: 0 auto;
	display: flex;
	justify-content: center;
	padding: 73px 0px 49px 0px;

	&__bl-1 {
		width: 326px;
		justify-content: left;
		margin-right: 52px;
		div {
		width: 70px;
    background-color: #F7992B;
    height: 5px;
		}
	}
	
	&__bl-2 {
		width: 600px;
		height: 300px;
		justify-content: left;
		padding-top: 36px;
	}
	
	&__text {
		text-transform: uppercase;
		font-family: "Oswald", Sans-serif;
		font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 1.285em;
    letter-spacing: 1.4px;
	}
	
	&__slogan {
		text-transform: uppercase;
		font-family: "Oswald", Sans-serif;
		font-size: 48px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.125em;
		letter-spacing: 2.4px;
		margin: 20px 0 18px 0;
	}

	&__header-description {
		color: #000000;
    font-family: "Montserrat", Sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 2.143em;
	}
	&__description {
		font-family: "Montserrat", Sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 2.143em;
	}
}

.cards {
	width: 100%;
	justify-content: center;
	&-box {
		display: flex;
		width: 1200px;
		justify-content: space-around;
		margin: 0 auto;
	}
	&__architecture,
	&__engineering,
	&__interior-desighn {
		display: flex;
		justify-content: center;
	}
	h1 {
		text-transform: uppercase;
		font-family: "Oswald", Sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 2.25em;
		letter-spacing: 1.2px;
		color: white;
		position: absolute;
    margin-top: 370px;
	}
}